*{
    margin: 0;
    padding: 0; 
}
.shopImg img{
    width: 100%;
}
.navBar{
    width:94%;
    position: absolute;
    top: 1.4em;
    left: 0;
    display: flex;
    z-index: 110;
    padding:0 0.8em;
    -webkit-box-pack: justify;
    justify-content: space-between;
}
.back{
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 100%;
    display: flex;
    -webkit-box-align: center;
    -webkit-box-pack: center;
    width: 2.1rem;
    height: 2.1rem;
    justify-content: center;
    line-height: 2.1rem;
}
.cart{
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 100%;
    display: flex;
    -webkit-box-align: center;
    -webkit-box-pack: center;
    width: 2.1rem;
    height: 2.1rem;
    justify-content: center;
    line-height: 2.1rem;
}
.btnBar{
    background-color: #fff;
    width: 100%;
    height: 55px;
    position: fixed;
    bottom: 0;
    z-index: 110;
    color: #999;
    display:flex;
    flex-direction:row;
    align-items: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-box-align: center;
    padding-left: 0.2rem;
    padding-right: 0.1rem;
    padding-bottom: env(safe-area-inset-bottom);
    box-shadow: rgba(0,0,0,0.15) 0 -0.01rem 0.06rem 0;

}
.btnBar div{
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    justify-content: center;
    -webkit-box-pack:center;
    align-items:center;
}
.btnBar button{
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    justify-content: center;
    -webkit-box-pack:center;
    align-items:center;
    background-color: transparent;
}
.btnlist{
    margin-top: -0.9rem;
    margin-left: -0.2rem;
    margin-right: -0.2rem;
    color: #999;
    font-size: 0.25rem;
    width: 3.02rem;
}
.btnlist i{
    -webkit-font-smoothing:antialiased;
    -webkit-text-stroke-width:0.3px;
    position: relative;
    left: 0.1rem;
}
.btnlist span{
    line-height: 1.5rem;
    margin-top: 2.4rem;
    margin-left: -1.1rem;
}
.btnRight{
    width: 32%;
    height: 2.5rem;
    bottom: 0.25rem;
    text-align: center;
    padding: 0;
}
.addCart {
    border-radius: 6rem 0 0 6rem;
    background-image:linear-gradient(to right,#ffc500,#ff9402);
    color: #fff;
}
.buyNow{
    border-radius: 0 6rem 6rem 0;
    background-image:linear-gradient(to right,#ff7a00,#fe560a);
    color: #fff;
}
.btnRight span{
    font-size: 0.9rem;
    font-weight: bold;
}