*{
    margin: 0;
    padding: 0;
}
html,body{
    background-color: #eee;
}
.cancel{
    display: flex;
    background-color: #fff;
    font-size: 12px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    padding: 0 20px;
    color: #333;
    position: absolute;
    right: 0px;
    top: 8px;
    
}
.am-search-cancel{
    display: none;
}
.am-search {
    border-bottom: 1px solid #dbdbdb;
    padding: 0 66px 0 12px !important;
    background-color: #fff !important;
    height: 45px !important;
}
.am-search-input {
    background-color: #e3e3e5 !important;
    height: 32px !important;
    padding-right: 30px !important;
    line-height: 50px !important;
}
.am-search-value {
    margin-top: 1.5px !important;
}
.sousuo{
    background-color: transparent;
    color: #999999;
    position: absolute;
    right: 80px;
    top:7px;
    font-size: larger;
    z-index: 150;
}
.am-search-input .am-search-synthetic-ph{
    display: none !important;
}
.am-search.am-search-start .am-search-input input[type="search"] {
    padding: 0 30px 0 56px !important;
    line-height: 35px !important;
}
.am-search-clear {
    right: 32px !important; 
    margin-top: 1.5px !important;
    background-size:14px 14px !important;
}
.tabNav{
    position: absolute;
    left: 10px;
    top: 40px;
    z-index: 101;
    padding: 10px;
}
.tabNav ul{
    background: #484d50;
    border-radius: 6px;
    width: 140px;
    overflow: hidden;
    color: #c7c8c9;
}
.tabNav li{
    border-top: 1px solid #707476;
    height: 35px;
    line-height: 35px;
    font-size: 14px;
}
.tabNav i{
    font-size: 22px;
    margin-left: 8px;
    margin-right: 8px;
}