*{
    padding: 0;
    margin: 0;
    touch-action: none;
}

nav{
    width: 100%;
    height: 37px;
    background-color: #ff852a;
    position: fixed;
    z-index: 1000;
}
.sli{
    height: 10px;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100%;
    position: relative;
    top:-14px;
}
.ic{
    height: 170px;
    width: 100%;
    background: rgb(255, 255, 255);
    overflow-x: auto;
    margin-top: -17px;
}
.reC{
    width: 100%;
    background-color: #f2f2f2;
    padding-bottom: 30%;
}
.reImg{
    width: 45%
}
.reP{
    text-align: center;
}
.reco{
    padding: 5% 0 5% 0;
    width: 42%;
}
.reItem{
    width: 45.5%;
    position: relative;
    background-color: #fff;
    overflow: hidden;
    border-radius: 3%;
    margin-left: 3%;
    margin-bottom: 2%;
    box-sizing: border-box;
    display: inline-flex;
    flex-direction: column;
    align-content: flex-start;
}
.reAP{
    padding-right: 4%;
    padding-left: 6%;
    text-decoration: none;
    height: 89px;
}
.reTitle{
    margin-top: 8%;
    height: 40%;
    position: relative;
    overflow: hidden;
    line-height: 140%;
    text-overflow: ellipsis;
    min-height: 20%;
}
.reTitleP{
    font-size: 0.95em;
    color: #333333;
}
.reImg{
    width: 100%;
    overflow: hidden;
}
.rePrice{
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    flex-direction: row;
    margin-top: 8.5%;
}
.reSign{
    font-size: 0.8em;
    color: #ff5500;
    margin-left: 2%;
}
.rePriceP{
    font-size: 1.1em;
    line-height: 90%;
    color: #ff5500;
    margin-left: 3%;
}
.rePayed{
    font-size: 0.8em;
    color: #999999;
    margin-top: 1%;
    margin-left: 3%;
}

